import React, { useEffect, useState } from 'react';
import { BrowserRouter as Router, Switch, Route, Link, Redirect } from 'react-router-dom'
import { faBars } from '@fortawesome/free-solid-svg-icons'

import { getAllSeasons } from './services'
import logo from './resources/images/logo.png'
import GlobalStyle from './components/GlobalStyle';
import { Nav, StyledLink, FontAwesome } from './components/Nav';
import LandingSection from './components/LandingSection'
import Seasons from './components/Seasons'
import Season from './components/Season'
import Caracters from './components/Caracters';
import Footer from './components/Footer';
import Form from './components/Form';
import Title from './components/Title';


const App = () => {
  const [season, setSeason] = useState([])
  const [seasonFilter, setSeasonFilter] = useState('')
  const [burger, setBurger] = useState(false)
  const [user, setUser] = useState(false)
  const [login, setLogin] = useState(true)

  useEffect(() => {
    getAllSeasons().then(res => {
      setSeason(prev => prev.concat(res.data))
    })
  }, [])

  return (
    <>
      <GlobalStyle />
      <Router>

        <header>
          <Nav onClick={() => burger === false ? setBurger(true) : setBurger(false)} burger={burger} login={login}>
            <Link to="/rick-and-morty"><img src={logo} alt="logo" width="50px" /></Link>
            <FontAwesome icon={faBars} size="2x" color="#E7E8E9" />
            <StyledLink to="/rick-and-morty/characters/1" burger={burger ? 1 : 0}>CHARACTERS</StyledLink>
            <StyledLink to="/rick-and-morty/seasons" primary="true" burger={burger ? 1 : 0}>SEASONS</StyledLink>
          </Nav>
        </header>

        <main>
          <article>
          {user ? <Redirect to="/rick-and-morty" /> : <Redirect to ="/rick-and-morty/signin" />}
            <Switch>
              <Route exact path="/rick-and-morty">
                <LandingSection />
              </Route>
              <Route exact path="/rick-and-morty/seasons">
                <Seasons setSeasonFilter={setSeasonFilter}/>
              </Route>
              <Route exact path="/rick-and-morty/seasons/:num">
                <Title title={'Season One'}/>
                <Season season={season} seasonFilter={seasonFilter}/>
              </Route>
              <Route exact path="/rick-and-morty/seasons/:num">
                <Title title={'Season Two'}/>
                <Season season={season} seasonFilter={seasonFilter}/>
              </Route>
              <Route exact path="/rick-and-morty/seasons/:num">
                <Title title={'Season Three'}/>
                <Season season={season} seasonFilter={seasonFilter}/>
              </Route>
              <Route exact path="/rick-and-morty/seasons/:num">
                <Title title={'Season Four'}/>
                <Season season={season} seasonFilter={seasonFilter}/>
              </Route>
              <Route path="/rick-and-morty/characters/:id">
                <Title title={'Caracters'}/>
                <Caracters />
              </Route>
              <Route path="/rick-and-morty/signin">
                <Form setLogin={setLogin}  setUser={setUser}/>
              </Route>
            </Switch>
          </article>
        </main>
        
          <Footer />
      </Router>
    </>
  )
}

export default App;
